.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(34,34,34);
    position: relative;
    width: 100vw;
    height: 500px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 1fr 3fr;
    gap: 0px 0px;
    grid-template-areas:
      ". title ."
      ". button ."
      ". description .";
}


.mobileWrapper {
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: 500px;
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    grid-template-rows: 1fr 1fr 3fr;
    gap: 0px 0px;
    grid-template-areas:
    ". title ."
    ". button ."
    ". description .";
}

h2{
    margin-top: 0px;
    margin-bottom: 0px;
}

.titleLabel{
    grid-area: title;
}

.buttonWrapper{
    position:relative;
    grid-area: button;
}

.descriptionWrapper{
    grid-area: description;
}

.descriptionWrapperMobile{
    grid-area: description;
}

.descriptionMobile{
    font-size: 1.5rem;
    font-weight: 200;
    color: #8C52FF;
}

.descriptionWrapper{
    font-size: 1rem;
    font-weight: 200;
    color: #8C52FF;
}

.customLabelMobile{
    grid-area: title;
    position: relative;
    top: 10px;
    width: 20rem;
    height: 5rem;
    margin: 0 auto;
    padding: 0;
}

.customLabelDesktop{
    grid-area: title;
    position: relative;
    top: 2.5rem;
    width: 30rem;
    height: 5rem;
    margin: 0 auto;
    padding: 0;
}

.ticketButton{
    width: 350px;
    margin:0 auto;
    display:block;
    height: 50px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    border: none;
    background-color:#F6CB4B;
    color: rgb(34,34,34);
    z-index: 3;
}
