.desktopWrapper{
    padding-top: 30px;
    top: 400vh;
    height: auto;
    max-width: 100%;
    background-color: rgb(34,34,34);
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
}

.mobileWrapper{
    padding-top: 30px;
    top: 400vh;
    height: auto;
    max-width: 100%;
    background-color: rgb(34,34,34);
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
}

.infoPhoto{
    overflow-x: hidden;
    width: 600px;
    height: 400px;
    display: inline;
}

.infoPhotoDiv{
    overflow-x: hidden;
    display: inline;
}

.mainText{
    display: inline;
    width: 300px;
}

.infoTitle{
    font-size: 30px;
    position: relative;
    left: 30px;
    top: 10px;
    color: #E3E3E3;
}


.infoAddress{
    font-size: 20px;
    position: relative;
    left: 30px;
    top: 10px;
    color: #8C52FF;
}

.infoTip{
    position: absolute;
    vertical-align: middle;
    right: 0px;
    z-index: 999;
    width: 100%;
    height: 150px;
    background-color: rgb(34,34,34);
}

.infoTipDesktop{
    vertical-align: middle;
    text-align: center;
    right: 0px;
    z-index: 999;
    width: 100%;
    height: 150px;
    background-color: rgb(34,34,34);
}

.infoTitleDesktop{
    font-size: 4rem;
    position: relative;
    left: 30px;
    top: 10px;
    color: #E3E3E3;
}

.infoAddressDesktop{
    font-size: 3rem;
    position: relative;
    left: 30px;
    top: 10px;
    color: #8C52FF;
}

.location{
    overflow-x: hidden;
    z-index: 800;
    padding-top: -100px;

}

.locationIcon{
    text-align: left;
    position: relative;
    top: 20px;
    left: 30px;
}

.yandexRef{
    color:  #F6CB4B;
    font-size: 22px;
    text-decoration: none;
}