.mobileWrapper{
    width: 100%;
    height: auto;
    background-color: #F6CB4B;
}

.desktopWrapper{
    width: 100%;
    overflow-x: hidden;
    height: auto;
    background-color: #F6CB4B;
}

.labelWrapper{
    padding-top: 40px;
    width: 100%;
    top: 360px;
    margin: 0 auto;
}

.label{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    color: rgb(34,34,34);
    padding-top: 10px;
    padding-bottom: 10px;
}




.sponsorWrapper{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 200px 200px;
  gap: 20px 0px;
  grid-template-areas:
    ". Logo28 ."
    ". Dorama ."
    ". Fuzetea ."
    ". Ivi ."
    ". Mosigra ."
    ". RedBull ."
    ". Skyeng ."
    ". Vkusvill ."
    ". Vsemaykiru ."
    ". . .";
}



.sponsorWrapperDesktop{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    "Label Logo28"
    "Dorama Fuzetea"
    "Ivi Mosigra"
    "RedBull Skyeng"
    "Vkusvill Vsemaykiru"
}

.Label{
    grid-area: Label;
}

.labelD{
    position: relative;
    text-align: center;
    font-size: 3rem;
    top: 40%;
