.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    width: 100%;
    height: 500px;
    padding-top: 150px;
    margin: 0 auto;
    overflow-x: hidden;
}

.locationLogo{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border:3px solid #8C52FF ;
}

.yandexRef{
    overflow-x: hidden;
    text-decoration: none;
    position: relative;
    right: 25%;
    bottom: 10%;
    font-size: 1.5rem;
    text-align: center;
}

.yandexRefDesktop{
    overflow-x: hidden;
    text-decoration: none;
    position: relative;
    right: 35%;
    bottom: 10%;
    font-size: 3rem;
    text-align: center;
}

.mobileWrapper{
    font-family: 'Open Sans', sans-serif;
    width: 100%;
    height: 500px;
    padding-top: 150px;
    margin: 0 auto;
    overflow-x: hidden;
}