.desktopWrapper {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: auto;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". first ."
      ". second ."
      ". third .";
}

  .mobileWrapper {
    font-family: 'Open Sans', sans-serif;
    display: grid;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: auto;
    grid-template-columns: 1fr 7fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    ". first ."
    ". second ."
    ". third .";
}

.firstItem{
    grid-area: first;
}

.secondItem{
    grid-area: second;
}

.thirdItem{
    grid-area: third;
}

.title{
    font-size: 1rem;
}

.customLabelDesktop{
    position: relative;
    width: 30rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;  
}

.customLabelMobile{
    position: relative;
    width: 20rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;  
}

.description{
    color: rgb(15,237,255);
    font-size: 2rem;
}

.descriptionMobile{
    color: rgb(15,237,255);
    font-size: 2rem;
    font-weight: 200;
}