.desktopWrapper{
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 5fr 2fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". buyTicketLabel ."
      ". imgCarousel ."
      ". mainText ."
      ". buyButton ."
      ". showMore .";
}

.mobileWrapper {
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
    background-color: rgb(34,34,34);
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 8fr 1fr;
    grid-template-rows: 1fr 5fr 2fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    ". buyTicketLabel ."
    ". imgCarousel ."
    ". mainText ."
    ". buyButton ."
    ". showMore .";
}

.buyTicket{
    grid-area: buyTicketLabel;
}

.carouselWrapper{
    grid-area: imgCarousel;
}

.carouselWrapperMobile{
    grid-area: imgCarousel;
}

.mainTextWrapper{
    grid-area: mainText;
    align-items: center;
}

.buttonWrapper{
    grid-area: buyButton;
}

.more{
    grid-area: showMore;
}

.buyTicket{
    text-align: center;
}

.buyTicketLink{
    text-decoration: none;
    color:#F6CB4B;
    font-size: 2rem;
}

.carouselPhoto{
    width: 25rem;
    height: 25rem;
    position: absolute;
    margin: 0 auto;
    z-index: 3;
}

.carouselPhotoMobile{
    width: 20rem;
    height: 20rem;
    position: absolute;
    margin: 0 auto;
    z-index: 3;
}


.carouselWrapperMobile{
    margin: 0 auto;
    width: 20rem;
    height: 20rem;
    z-index: 3;
    display: flex;
    overflow: hidden
}

.carouselWrapper{
    margin: 0 auto;
    width: 25rem;
    height: 25rem;
    z-index: 3;
    display: flex;
    overflow: hidden
}

.styledTextWrapper{
    width: 23rem;
    height: 6rem;
    margin: 0 auto;
    padding: 0;
}

.styledTextWrapper2{
    width: 20.5rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;
}

.styledTextWrapper3{
    width: 21.5rem;
    height: 4rem;
    margin: 0 auto;
    padding: 0;
}

.ticketButton{
    width: 350px;
    margin:0 auto;
    display:block;
    height: 50px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    border: none;
    background-color: #8C52FF ;
    color: rgb(230, 230, 230);
    z-index: 3;
}

.moreLabel{
    font-size: 2rem;
    color:#F6CB4B;
    text-align: center;
}